#contactContainer {
  min-height: 100vh;
  max-width: 100vw;
  padding: 5% 0;
  overflow: hidden;
  display: -ms-grid;
  display: grid;
      grid-template-areas: "logo cta ." "  .  cta contact";
  -ms-grid-rows: 2fr 1fr;
  grid-template-rows: 2fr 1fr;
  -ms-grid-columns: 1fr 2fr 1fr;
  grid-template-columns: 1fr 2fr 1fr;
  position: relative;
}

#contactContainer .content {
  font-family: proxima, sans-serif;
}

#contactContainer .titles {
  font-family: "Krona One", sans-serif;
}

#contactContainer > .logo {
  -ms-grid-row: 1;
  -ms-grid-column: 1;
  grid-area: logo;
  max-width: 175px;
  padding: 0;
  margin: 0 auto;
  z-index: 1;
  margin-top: 50px;
}

#contactContainer > #info {
  height: 150px;
  color: #f1f1f1;
  text-align: left;
  margin: 0 auto;
  -ms-grid-row: 2;
  -ms-grid-column: 3;
  grid-area: contact;
  z-index: 1;
}

#contactContainer > #info > span {
  margin-bottom: 10px;
}

#contactContainer #backgroundImg:before {
  content: "";
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  background-color: #0d1128;
  top: 0;
  left: 0;
  opacity: 0.87;
  z-index: 0;
}

#contactContainer > #backgroundImg {
  background-repeat: no-repeat;
  background-size: cover;
  background-size: 100% 100%;
  min-width: 100vw;
  min-height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

#callToAction {
  z-index: 1;
  margin: 0 auto;
  padding: 0;
  width: 50vw;
  -ms-grid-row: 1;
  -ms-grid-row-span: 2;
  -ms-grid-column: 2;
  grid-area: cta;
  font-size: 1.25em;
  margin-top: 50px;
}

#callToAction > p,
#callToAction h1,
#callToAction label {
  color: #f1f1f1;
}

#callToAction hr {
  width: 75%;
  margin: 0 auto;
}

#callToAction #desc {
  margin: 5vh 0 2vh 0;
  text-align: justify;
  font-size: 1em;
}

#callToAction #inputs {
  padding-bottom: 15px;
}

#callToAction #inputs > label {
  text-align: left;
}

#callToAction #inputs input {
  width: 100%;
  height: 2rem;
  font-size: 1em;
  display: inline-block;
  border: none;
  background: #f1f1f1;
  overflow: auto;
  padding: 2px;
  color: black;
  margin: 5px 0 10px 0;
  border-radius: 5px;
}

#callToAction #inputs textarea[name="body"] {
  width: 100%;
  height: 4rem;
  overflow: auto;
  resize: none;
  padding: 3px;
  font-size: 1em;
  margin: 5px 0 10px 0;
  border-radius: 5px;
  height: 150px;
}

#callToAction #inputs > button {
  height: 2.5em;
  color: #000;
  font-size: 0.8em;
  border-radius: 5px;
  width: 150px;
  font-size: 1.2rem;
  font-weight: 800;
  z-index: 2;
  position: relative;
  background-color: #85dbd9;
  border-radius: 5px;
  font-family: 'Krona One', sans-serif;
}

#callToAction #inputs > button:hover {
  transform: scale(1.1);
  border: 1px solid #f1f1f1;
}

@media only screen and (max-width: 1000px) {
  #contactContainer {
    display: flex;
    flex-direction: column;
    padding: 0;
  }
  #contactContainer > #logo {
    margin: 0;
    padding: 0;
  }
  #contactContainer > #callToAction {
    width: calc(100vw - 2em - 10%);
    flex: 1 1;
    margin-top: 0;
  }
  #contactContainer > #callToAction > h1 {
    font-size: 1.9em;
  }
  #contactContainer > #info {
    height: 90px;
    padding: 0;
    width: calc(100vw - 2em - 10%);
    line-height: 1.5em;
  }
}
